
<template>
  <div class="content">
    <order-info v-if="data.resData" :parentData = "data"></order-info>

    <logistics-information v-if="data.resData" :parentData = "data"></logistics-information>
  </div>
</template>

<script>
/*
 * 退款进程 订单详情
*/
import orderInfo from './order-info';

/*
 * 退款进程 退款进程信息
*/
import logisticsInformation from './logistics-information';

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/**
 * tost提醒
 */
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);

/*
 * 订单详情 退款进程
*/
export default {
  name: 'trackingOrders',
  components: {
    orderInfo,
    logisticsInformation
  },
  data() {
    return {
      /**
       * {data} 基本信息
       * {memberId} 会员id
       * {orderProductId} 网单id
       */
      data: {
        memberId: '',
        orderProductId: '',
        resData: null
      }
    }
  },
  created() {
    const context = this;
    const _query = context.$route.query;
    context.data.memberId = _query.memberId;
    context.data.orderProductId = _query.orderProductId;
  },
  mounted() {
    const context = this;
    /**
     * @description  订单详情 退款进程信息（逆向物流）
     * @param {memberId} 会员id
     * @param {orderProductId} 网单id
     * 返回接口请求数据
    */
    context.getDetail();
  },
  methods: {
    /**
     * @description  订单详情 退款进程信息（逆向物流）
     * @param {memberId} 会员id
     * @param {orderProductId} 网单id
     * 返回接口请求数据
    */
    getDetail () {
      const context = this;
      const data = {
        orderProductId: context.data.orderProductId,
        memberId: context.data.memberId
      }
      personalApi.getRefundProcessDetail(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          // 返回的退款节点反转排列
          _resData.data.orderWorkFlowList = _resData.data.orderWorkFlowList.reverse();
          context.data.resData = _resData.data || {};
        } else {
          Toast.fail(_resData.message);
        }
      })
    }
  },
}
</script>

